@tailwind base;
@tailwind components;
@tailwind utilities;

/* --- GLOBAL LAYOUT & SCROLLING --- */
html, body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  background-color: #fffbf0;
  overflow: hidden;
  overscroll-behavior-y: none;
  -webkit-text-size-adjust: 100%;
}

body {
  font-family: 'Outfit', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #514d46;
}

#app-scroll-container {
  height: 100%;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}

#root {
  overflow-x: clip;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

/* --- UTILITIES --- */
::selection {
  background-color: #f4e799;
  color: #514d46;
}

::-webkit-scrollbar { width: 10px; }
::-webkit-scrollbar-track { background: #f2e9d9; }
::-webkit-scrollbar-thumb {
  background: #d35153;
  border-radius: 5px;
  border: 2px solid #f2e9d9;
}
::-webkit-scrollbar-thumb:hover { background: #b94547; }

.no-scrollbar::-webkit-scrollbar { display: none; }
.no-scrollbar { -ms-overflow-style: none; scrollbar-width: none; }

input[type=range]::-webkit-slider-thumb { pointer-events: auto; }
input[type=range]::-moz-range-thumb { pointer-events: auto; }

/* --- ANIMATIONS --- */
@keyframes deal-bottom {
  0% { transform: translateY(100px); }
  100% { transform: translateY(0); }
}

@keyframes tear-off {
  0% { transform: rotate(0deg); }
  20% { transform: rotate(-5deg); }
  40% { transform: rotate(5deg); }
  60% { transform: rotate(-2deg); }
  100% {
    transform: translateY(150%) rotate(10deg);
    opacity: 0;
  }
}

/* Heavy Slam - Reduced scale to prevent "flash" */
@keyframes stamp {
  0% {
    opacity: 0;
    /* Reduced from 5 to 2.5 to stop it filling the screen/flashing */
    transform: scale(2.5) rotate(-12deg);
  }
  100% {
    opacity: 1;
    transform: scale(1) rotate(-12deg);
  }
}

@keyframes ticker-scroll {
  0% { transform: translateX(0); }
  100% { transform: translateX(-100%); }
}

/* --- ANIMATION HELPERS --- */
.animate-deal-bottom {
  animation: deal-bottom 0.6s cubic-bezier(0.2, 0.8, 0.2, 1) both;
}

.animate-tear-off {
  animation: tear-off 0.8s ease-in forwards;
}

.animate-stamp {
  /* Heavy impact ease-out */
  animation: stamp 0.3s cubic-bezier(0.25, 1, 0.5, 1) forwards;
  opacity: 0;
  animation-delay: 0s;
}

/* --- TICKER --- */
.ticker-track {
  display: inline-block;
  white-space: nowrap;
  animation: ticker-scroll 40s linear infinite;
  position: relative;
  will-change: transform;
  transform: translate3d(0, 0, 0);
  backface-visibility: hidden;
  perspective: 1000px;
}

.ticker-track::after {
  content: attr(data-dup);
  position: absolute;
  left: 100%;
  top: 0;
  white-space: nowrap;
}

.ticker-track:hover {
  animation-play-state: paused;
}

/* --- SOLD STAMP DUST (SUBTLE STONE COLOR) --- */
@keyframes puff-explode {
  0% {
    opacity: 0;
    transform: translate(0, 0) scale(0.6);
  }
  40% {
    /* Slow fade-in to prevent flashing */
    opacity: 0.5;
    transform: translate(var(--tx), var(--ty)) scale(1.1);
  }
  100% {
    opacity: 0;
    /* Gentle drift distance */
    transform: translate(calc(var(--tx) * 1.5), calc(var(--ty) * 1.5)) scale(0.8);
  }
}

.dust-puff {
  position: absolute;
  pointer-events: none;
  opacity: 0; /* Hidden by default */
  animation: puff-explode 0.5s ease-out forwards;
  /* Syncs with the impact (approx 0.25s) */
  animation-delay: 0.25s; 
}
